/* 
   !    input
   TODO: index_1 - . .,    , index_2 - . .,    
*/
function Calculation(value, index_1, index_2) {
   let result

   switch(index_1) {
               
      case 0:     // 
         result = fromGrad(value, index_2)
         break
      case 1:     // 
         result = fromDegree(value, index_2)
         console.log(result)
         break
      case 2:     // 
         result = fromRadian(value, index_2)
         break
      case 3:     //  
         result = fromAngularSecond(value, index_2)
         break 
      case 4:     //  
         result = fromArcMinute(value, index_2)
         break   
      default: 
         alert("  ")           
   }

   return result
}

//  
function fromGrad(value, index) {
   let result

   switch(index) {
      case 0:  // 
         result = value
         break
      case 1:   // 
         result = value * (180.0 / 200.0)
         break
      case 2:   // 
         result = value * (Math.PI / 200.)
         break
      case 3:   //  
         result = value * 3240
         break
      case 4:   //  
         result = value * 54
         break
      default: 
         alert("  ")
      
   }
   return result
}

//  
function fromDegree(value, index) {
   let result

   switch(index) {
      case 0:  // 
         result = value * (200.0 / 180.0)
         break
      case 1:   // 
         result = value
         break
      case 2:   // 
         result = value * (Math.PI / 180.)
         break
      case 3:   //  
         result = value * 3600
         break
      case 4:   //  
         result = value * 60
         break
      default: 
         alert("  ")
         break
   }
   return result
}

//  
function fromRadian(value, index) {
   let result

   switch(index) {
      case 0:  // 
         result = value * (200.0 / Math.PI)
         break
      case 1:   // 
         result = value * (180.0 / Math.PI)
         break
      case 2:   // 
         result = value
         break
      case 3:   //  
         result = value * (3600.0 * 180.0) / Math.PI
         break
      case 4:   //  
         result = value * (60.0 * 180.0) / Math.PI
         break
      default: 
         alert("  ")
   }
   return result
}

//   
function fromAngularSecond(value, index) {
   let result

   switch(index) {
      case 0:  // 
         result = value / 3240.0
         break
      case 1:   // 
         result = value / 3600.0
         break
      case 2:   // 
         result = value * Math.PI / (180.0 * 3600)
         break
      case 3:   //  
         result = value
         break
      case 4:   //  
         result = value / 60.0
         break
      default: 
         alert("  ")
   }
   return result
}

//   
function fromArcMinute(value, index) {
   let result

   switch(index) {
      case 0:  // 
         result = value / 54.0
         break
      case 1:   // 
         result = value / 60.0
         break
      case 2:   // 
         result = value * Math.PI / (60.0 * 180.0)
         break
      case 3:   //  
         result = value * 60
         break
      case 4:   //  
         result = value
         break
      default: 
         alert("  ")
      
   }
   return result
}

export {Calculation}